.checkout-item {
  width: 100%;
  display: flex;
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 15px 0;
  font-size: 20px;
  align-items: center;

  .image-container {
    width: 23%;
    padding-right: 15px;

    img {
      width: 100%;
      height: 100%;
    }
  }
  .name,
  .quantity,
  .price {
    width: 23%;
  }

  .quantity {
    // padding-left: 20px;
    display: flex;
    align-items: center;

    .left,
    .right {
      cursor: pointer;
    }

    & > * {
      padding: 0 0.2rem;
    }
  }

  .remove-button {
    padding-left: 12px;
    cursor: pointer;
  }
}
