@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  /* font-size: 62.5%; */
  box-sizing: border-box;
}

body {
  padding: 2rem 6rem;
  font-family: "Open Sans Condensed", sans-serif;
}

a {
  text-decoration: none;
  color: black;
}

.homepage{display:flex;flex-direction:column;align-items:center;padding:20px 80px}

.card{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:2px solid rgba(0,0,0,0.1);margin:0 7.5px 15px;overflow:hidden}.card:hover{cursor:pointer}.card:first-child{margin-right:7.5px}.card:last-child{margin-left:7.5px}.card__content-box{height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:2px solid rgba(0,0,0,0.1);position:absolute;opacity:0.9;background-color:#fff;text-transform:uppercase}.card__background{width:100%;height:100%;background-position:center;background-size:cover;background-repeat:no-repeat}.card__background:hover{transform:scale(1.1);transition:all 6s cubic-bezier(0.25, 0.46, 0.45, 0.94)}.card__title{font-weight:bold;margin-bottom:6px;font-size:22px;color:#4a4a4a}.card__subtitle{font-weight:lighter;font-size:16px}.card.card--large{height:360px}

.menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.collection-preview{display:flex;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:28px;margin-bottom:25px}.collection-preview .preview{display:flex;justify-content:space-between}

.collection-item{flex:0 0 22%;display:flex;flex-direction:column;height:350px;align-items:center;position:relative}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .collection-footer{width:100%;height:5%;display:flex;justify-content:space-between;font-size:18px}.collection-item .collection-footer .name{width:90%;margin-bottom:15px}.collection-item .collection-footer .price{width:10%}.collection-item .custom-button{position:absolute;top:255px;opacity:0.85;display:none}.collection-item:hover .custom-button{display:block}.collection-item:hover .image{opacity:0.8}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:"Open Sans Condensed";font-weight:bolder;border:1px solid transparent;cursor:pointer;display:flex;justify-content:center}.custom-button.custom-button--blue{background-color:#4267b2}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:1px solid white}.custom-button:focus{outline:none}.custom-button:hover{background-color:white;color:black;border:1px solid black}

.collection-preview{display:flex;flex-direction:column}

.collection-page{display:flex;flex-direction:column}.collection-page .title{font-size:38px;margin:0 auto 30px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.collection-page .items .collection-item{margin-bottom:30px}


.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px;position:relative}.header .logo-container{height:100%;width:70px;padding:25px}.header .options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end}.header .options .option{padding:10px 15px;font-size:1.5rem;text-transform:capitalize}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:0px;z-index:5}.cart-dropdown .empty-message{text-align:center;text-transform:capitalize;margin-top:2rem}.cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow-y:scroll}.cart-dropdown button{margin-top:auto}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.sign-container{display:flex;justify-content:space-around}.sign-container>*{flex:0 0 40%}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type="password"]{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.sign-in{display:flex;flex-direction:column}.sign-in .title{margin:1rem 0}.button-group{display:flex;justify-content:space-between;align-items:center}

.sign-up{display:flex;flex-direction:column}.sign-up .title{margin:1rem 0}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0;font-size:20px;align-items:center}.checkout-item .image-container{width:23%;padding-right:15px}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{display:flex;align-items:center}.checkout-item .quantity .left,.checkout-item .quantity .right{cursor:pointer}.checkout-item .quantity>*{padding:0 0.2rem}.checkout-item .remove-button{padding-left:12px;cursor:pointer}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto 0}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page button{margin-top:1rem;margin-left:auto}.checkout-page .total{margin-top:30px;margin-left:auto;font-size:36px}

